.MakeUC{
  background: black;
  width: 100vw;
  height: 150vh;
  overflow: hidden;
}
article{
    display: flex;
    width: 50vw;
}
.MakeUC-container{
  position: absolute;
  margin-top: -150vh;
  margin-left: 25vw;
}
.parallax {
  /* The image used */
  background-image: url("https://preview.redd.it/wyajhva6ir551.png?width=1080&format=png&auto=webp&s=83e2b9bfd9df9b99dcf5fd54b4df3d42a81d965c");

  /* Set a specific height */
  min-height: 100%;
  opacity: 0.5;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.fontfix{
  font-family: Impact, fantasy;
}