.OurProduct{
    background: black;
    
    height: 70vh;
    width:100vw;
    overflow: hidden;
}
.soldier{
    width:40vw;
    height: fixed;
    animation: shake 8s;
    animation-iteration-count: infinite;
}
@keyframes shake {
    0% { transform: translate(1px, 1px) rotate(0deg); }
    10% { transform: translate(-1px, -2px) rotate(-1deg); }
    20% { transform: translate(-3px, 0px) rotate(1deg); }
    30% { transform: translate(3px, 2px) rotate(0deg); }
    40% { transform: translate(1px, -1px) rotate(1deg); }
    50% { transform: translate(-1px, 2px) rotate(-1deg); }
    60% { transform: translate(-3px, 1px) rotate(0deg); }
    70% { transform: translate(3px, 1px) rotate(-1deg); }
    80% { transform: translate(-1px, -1px) rotate(1deg); }
    90% { transform: translate(1px, 2px) rotate(0deg); }
    100% { transform: translate(1px, -2px) rotate(-1deg); }
  }
.OurProductContent{
    display: flex;
}
.GameBtn{
    width: 100px;
    height: 50px;
    border-radius: 20%;
    background: chartreuse;
    margin-left: 10vw;
}
.content {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }
  .slider-wrapper {
    font-size: 20px;
    color:white;
    font-weight: bold;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .slider{
    height: 25px;
    padding-left:15px;
    overflow: hidden;
  }
  .slider div {
    color:#fff;
    height: 50px;
    margin-bottom: 50px;
    padding: 2px 15px;
    text-align: center;
    box-sizing: border-box;
  }
  .slider-text1 {
    background: lightgreen;
    animation: slide 5s linear infinite;
  }
  .slider-text2 {
    background: rgb(56, 110, 114);
  }
  .slider-text3 {
    background: rgb(68, 131, 202);
  }
  @keyframes slide {
    0% {margin-top:-300px;}
    5% {margin-top:-200px;}
    33% {margin-top:-200px;}
    38% {margin-top:-100px;}
    66% {margin-top:-100px;}
    71% {margin-top:0px;}
    100% {margin-top:0px;}
  }
.fontfix{
    font-family: Impact, fantasy;
}